@import "~antd/dist/antd.css";

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.size-map {
  max-width: 200px;
  margin: 0 auto;
}
.size-item {
  display: inline-block;
  width: 50px;
  height: 50px;
  background-color: #eee;
  border-radius: 50%;
  text-align: center;
  line-height: 50px;
  margin: 5px;
  font-weight: bold;
  color: #3d4455;
  cursor: pointer;
}
.size-item-active {
  background: #3d4455;
  color: #fff;
}
.cart-content {
  min-height: 75%;
}
.cart-item {
  width: 100%;
  display: flex;
  margin: 10px 5px;
  align-items: center;
}
.description {
  color: #3d4455;
  font-size: 16px;
  margin-left: 5px;
  width: 100%;
}
.description-number {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
